import React, { Fragment, useState } from 'react';
import styles from './BetList.module.css'
import { Skeleton } from '@mui/material';


interface Props {
    id : number;
    expanded : boolean;
}

function loadData(setLoaded : Function) {
    setTimeout(() => {
        setLoaded(true);
    }, 3000)
}

const PropListBody: React.FC<Props> = (props) => {
    const [loaded, setLoaded] = useState<boolean>(false);
    let cn;
    if (props.expanded) {
        cn = styles.bodyExpanded;
        if (!loaded) {
            loadData(setLoaded);
        }    
    } else {
        cn = styles.body;
    }
    if (!loaded && props.expanded) {
        return (
            <Fragment>
                <Skeleton variant="text" sx={{ fontSize: '1rem' }}></Skeleton>
                <Skeleton variant="text" sx={{ fontSize: '1rem' }}></Skeleton>
                <Skeleton variant="text" sx={{ fontSize: '1rem' }}></Skeleton>
                <Skeleton variant="text" sx={{ fontSize: '1rem' }}></Skeleton>
                <Skeleton variant="text" sx={{ fontSize: '1rem' }}></Skeleton>
                <Skeleton variant="text" sx={{ fontSize: '1rem' }}></Skeleton>
            </Fragment>
        );
    } else {
        return <div className={cn}></div>
    }
};

export default PropListBody;